<template>
  <!-- 주문정보 상세 입력-->
  <div class="container">
    <div class="py-4 text-center">
      <h2>주문정보 입력</h2>
    </div>
    <hr class="my-4" style="padding-bottom: 15px" />
    <div class="row g-5">
      <div class="col">
        <form class="needs-validation" novalidate>
          <div class="row g-3">
            <!-- id oAddr로 설정 (임시) -->
            <div class="col-2">
              <label for="oAddr" class="form-label">주소</label>
            </div>
            <div class="col-12">
              <input
                type="text"
                class="form-control"
                id="oAddr"
                placeholder="봉수로250"
                value=""
                required
                disabled
              />
            </div>
            <p></p>

            <!-- 전화번호 입력 -->

            <div class="col-2">
              <label for="oTel" class="form-label mr-">전화번호</label>
            </div>
            <div class="input-group mb-3">
              <input
                type="tel"
                class="form-control"
                placeholder="010-1234-1234"
                aria-label="Recipient's username"
                aria-describedby="button-addon2"
              />
              <button
                class="btn btn-outline-secondary"
                type="button"
                id="button-addon2"
              >
                변경
              </button>
            </div>
            <p class="text-start text-body-secondary" style="font-size: 13px">
              * 주소를 정확히 입력해주세요. 잘못 입력된 경우 재배달 및 환불이
              되지 않습니다.<br />
              * 부재시 여러 번 통화 시도 후 가까운 곳으로 임의로 배달될 수
              있습니다.
            </p>
            <!-- 간격 조정 -->
            <hr class="my-4" />

            <div class="col-2">
              <label for="oMsg" class="form-label">요청사항</label>
            </div>
            <div class="input-group">
              <textarea
                class="form-control"
                aria-label="With textarea"
              ></textarea>
            </div>
            <div class="col-12">
              <b-form-textarea
                id="textarea"
                v-model="text"
                rows="3"
                max-rows="3"
              ></b-form-textarea>
              <!-- <pre class="mt-3 mb-0">{{ text }}</pre> -->
            </div>
            <p></p>
          </div>
          <!-- 친구들에게 결제 요청 -->
          <b-button
            @click="PaymentInfo"
            class="w-100 btn btn-primary btn-lg"
            variant="primary"
            >결제하기</b-button
          >
          <p></p>
          <!-- <b-modal id="bv-modal-example" hide-footer> -->
          <!-- 결제중인 멤버들 (모달)-->
          <b-modal
            v-model="show"
            title="결제 현황"
            ok-only="true"
            ok-disabled="true"
          >
            <!-- 전체 결제 완료가 되면 ok 활성화 -->
            <b-container fluid>
              <!-- 모달창 내용 -->
              <ul class="list-group mb-3">
                <C_OPavator></C_OPavator>
              </ul>
            </b-container>
            <template #modal-footer>
              <div class="w-100">
                <i class="bi bi-arrow-clockwise"></i>
                <b-button
                  variant="primary"
                  size="sm"
                  class="float-right"
                  @click="show = false"
                  value="결제완료"
                >
                  결제 완료
                </b-button>
                <b-button
                  variant="primary"
                  size="sm"
                  class="float-right"
                  @click="show = false"
                >
                  결제 완료
                </b-button>
              </div>
            </template>
          </b-modal>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import C_OPavator from "@/components/client/C_OPavator.vue";
import axios from "axios";

export default {
data() {
  return {
    show: false,
    paymentInfo: [], // 결제 정보를 저장할 변수
    
  };
},
components: {
  C_OPavator,
},
methods: {
  async PaymentInfo() {
    try {
      const onum = 1001;
      const response = await axios.post('http://localhost:8888/api/paymentInfo', {onum: onum}); // 백엔드에서 결제정보를 받아옴
      this.paymentInfo = response.data;
      console.log(this.paymentInfo);

      for (const member of this.paymentInfo) {
        const { mnum, memail, mname, onum, dpprice, mtel} = member;

        // 이메일로 보낼 링크 생성
        const paymentLink = `http://localhost:8082/C_payrequest?mnum=${mnum}&memail=${memail}&mname=${mname}&onum=${onum}&dpprice=${dpprice}&mtel=${mtel}`;

        // 이메일 보내기
        await this.sendEmail(memail, 'Payment Link', `Hello ${mname}, here is your payment link: ${paymentLink}`);
      }

      // 이메일 보내기 완료 후 작업
      console.log('Emails sent successfully');

      
    } catch (error) {
      console.error('Failed to fetch payment info:', error);
    }
  },
  async sendEmail(to, subject, body) {
    try {
      // 이메일 보내기 API 호출 
      const response = await axios.post('http://localhost:8888/api/sendEmail', { to, subject, body });
      console.log('Email sent to:', to);
      console.log('Response from server:', response.data);
    } catch (error) {
      console.error('Failed to send email to:', to, error);
    }
  },
  
},
};
</script>

<style scoped></style>
